.plot-container {
    padding: 10px;
}

.plot-contents-container {
    display: flex;
    align-items: center;
    column-gap: 30px;
    /* width: calc(109% - 1px); */
    width: calc(100% - 1px);
    overflow: auto;

}

.main-plot-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.left-main-index {
    margin-top: auto;
    margin-bottom: auto;
    writing-mode: vertical-lr;
    font-weight: 700;
    transform: rotate(-180deg);
}

.main-plot-column-container {
    list-style-type: none;
    width: calc(94% - 1px);
}

.main-plot-column-name {
    margin-bottom: 30px;
    text-align: center;
    font-weight: 700;
}

.main-plot-column-index-container {
    display: flex;
    width: 100%;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    padding: 15px;
    border-radius: 6px;
    border: solid 1px;
}

.main-plot-column-index {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    /* width: 20%; */
    /* text-align: center; */
    /* line-height: 40px; */
}

.main-plot-first-category-container {
    display: flex;
    width: inherit;
}

.main-plot-first-category-index {
    writing-mode: vertical-lr;
    transform: rotate(-180deg);
    margin-top: auto;
    margin-bottom: auto;
    width: fit-content;
    white-space: nowrap;
    font-weight: 600;
}

.main-plot-first-category-contents-container {
    padding-left: 10px;
    list-style-type: none;
    width: inherit;
}

.main-plot-level-data-container {
    width: calc(100% - 1px);
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    padding: 15px;
    border-radius: 6px;
    border: solid 1px;
}

.main-plot-level {
    /* margin-right: 30px; */
    white-space: nowrap;
    text-align: center;
    line-height: 40px;
}

.main-plot-data {
    display: flex;
    justify-content: center;
    align-items: center;
    width: fit-content;
    cursor: pointer;
}

.main-plot-second-category-container {
    display: flex;
    width: inherit;
}

.main-plot-second-category-index {
    writing-mode: vertical-lr;
    transform: rotate(-180deg);
    margin-top: auto;
    margin-bottom: auto;
    width: fit-content;
    white-space: nowrap;
    font-weight: 600;
}

.main-plot-second-category-contents-container {
    padding-left: 10px;
    list-style-type: none;
    margin-top: 30px;
    width: inherit;
}

.main-plot-third-category-container {
    display: flex;
    width: inherit;
}

.main-plot-third-category-index {
    writing-mode: vertical-lr;
    transform: rotate(-180deg);
    margin-top: auto;
    margin-bottom: auto;
    width: fit-content;
    white-space: nowrap;
    font-weight: 600;
}

.main-plot-third-category-contents-container {
    padding-left: 10px;
    list-style-type: none;
    margin-top: 30px;
    width: inherit;
}

.legends-container {
    display: flex;
    column-gap: 10px;
    list-style-type: none;
    padding-left: 0px;
    margin-top: 90px;
}

.legends-content {
    display: flex;
    align-items: center;
}


@media (max-width: 844px) {

    .plot-contents-container {
        column-gap: 0px;
    }

    .left-main-index {
        font-size: 14px;
    }

    .main-plot-first-category-index {
        font-size: 13px;
    }

    .main-plot-second-category-index {
        font-size: 13px;
    }

    .main-plot-third-category-index {
        font-size: 13px;
    }

    .main-plot-column-index-container {
        padding: 5px;
    }

    .main-plot-column-index {
        font-size: 11px;
    }

    .main-plot-level-data-container {
        padding: 5px;
    }

    .main-plot-svg {
        width: 15px;
        height: 15px;
    }

    .main-plot-level {
        font-size: 12px;
        margin-right: 6px;
    }

    .main-plot-active {
        r: 6;
    }

    .main-plot-upgrade-a {
        r: 6;
        stroke-width: .5px;
    }

    .main-plot-upgrade-b {
        r: 3;
    }
}


/* width */
::-webkit-scrollbar {
    width: 10px;
    height: 8px;

}


/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 999px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}